@at-root {
  @import "../helpers/typography";

  @mixin element($tags...) {
    @each $tag in $tags {
      .pr-#{$tag}, .pr-copy #{$tag} {
        @content; } } }




  //------------------------------------------
  // Block elements
  //------------------------------------------

  @for $i from 1 through 6 {
    @include element(h#{$i}) {
      @include heading($i); } }



  @include element(p, pre, ul, ol) {
    @include paragraph; }



  @include element(pre) {
    font-size:     0.9em;
    overflow:      auto;
    white-space:   pre;
    border-radius: 0.3em;

    > code {
      padding: 1rem !important; } }



  @include element(ul, ol) {
    margin-left : 2em;
    list-style  : disc; }



  //------------------------------------------
  // Inline elements
  //------------------------------------------

  @include element(em, i) {
    font-style : italic; } }




