@at-root {

  @import "node_modules/sass-toolkit/stylesheets/toolkit";
  @import "addon/styles/mixins";



  ////------------------------------------------
  .xBlock {
    //------------------------------------------

    // ----- Layout -----
    padding : 0.5rem;

    // ----- Looks -----
    border        : 1px solid grey;
    border-radius : 2px;
    background    : #eee;

    // ----- Content layout -----
    &._static .xBlock-image-wrapper {
        margin-bottom: 1rem; }


    // ----- RWD -----
    $breakpoint: 500;

    &:not(:empty) {
      @include eq-to($breakpoint) {
        > .xBlock-image-wrapper {
          margin-bottom: 1rem; } }

      @include eq-from($breakpoint) {
        display : flex;

        > .xBlock-image-wrapper {
          width        : 30%;
          flex-shrink  : 0;
          margin-right : 1rem; } } } }





  ////------------------------------------------
  .xBlock-image-wrapper-intrinsic-ratio {
    //------------------------------------------

    // ----- Layout -----
    @include intrinsic-ratio(640 / 480);

    // ----- Looks -----
    background: grey; }




  ////------------------------------------------
  .xBlock-content {
    //------------------------------------------

    // ----- Typography -----
    @extend .pr-copy; } }
